let pagina = 1;

const cita = {
  nombre: "",
  fecha: "",
  hora: "",
  servicios: [],
};

document.addEventListener("DOMContentLoaded", function () {
  iniciarApp();
});

function iniciarApp() {
  mostrarServicios();

  //resalta el DIV actual segun el tab al que se presiona

  mostrarSeccion();
  //Ocultar o muestra una seccion segun el tab al que se presione
  cambiarSeccion();

  //Cambiar de pagina con boton paginador

  paginaSiguiente();
  paginaAnterior();

  //Paginador
  paginador();

  //Validar datos Resumen(o mensaje de error en caso de no pasar la validacion)
  mostrarResumen();

  //Validar datos de informacion de cliente
  nombreCita();

  // Almacena la fecha de la cita en el objeto
  fechaCita();

  // Almacena la hora
  horaCita();

  // Desabilita dias pasados
  desabilitaFechaAnterior();
}

function mostrarSeccion() {
  //Eliminar mostrar-seccion de la seccion anterior
  const seccionAnterior = document.querySelector(".mostrar-seccion");
  if (seccionAnterior) {
    seccionAnterior.classList.remove("mostrar-seccion");
  }

  const mostrarSeccion = document.querySelector(`#paso-${pagina}`);
  mostrarSeccion.classList.add("mostrar-seccion");

  //Eliminar la clase actual en el tab anterior

  const tabAnterior = document.querySelector(".tabs .actual");
  if (tabAnterior) {
    tabAnterior.classList.remove("actual");
  }

  //Resalta el Tab Actual
  const tab = document.querySelector(`[data-paso="${pagina}"]`);
  tab.classList.add("actual");
}

function cambiarSeccion() {
  const enlaces = document.querySelectorAll(".tabs button");

  enlaces.forEach((enlace) => {
    enlace.addEventListener("click", (e) => {
      e.preventDefault();
      pagina = parseInt(e.target.dataset.paso);

      //Llamar la funcion de mostrar seccion
      mostrarSeccion();
      paginador();
    });
  });
}

async function mostrarServicios() {
  try {
    const url = "http://localhost:3000/servicios.php";

    const resultado = await fetch(url);
    const db = await resultado.json();

    // console.log(db);

    // const { servicios } = db;

    db.forEach((servicio) => {
      const { id, nombre, precio } = servicio;

      //DOM Scripting

      //Generar nombre servicio
      const nombreServicio = document.createElement("P");
      nombreServicio.textContent = nombre;
      nombreServicio.classList.add("nombre-servicio");

      // Generar precio servicio
      const precioServicio = document.createElement("P");
      precioServicio.textContent = `$ ${precio}`;
      precioServicio.classList.add("precio-servicio");

      //Generar contenedor de servicio

      const servicioDiv = document.createElement("DIV");
      servicioDiv.classList.add("servicio");

      //Agregando ID a contenedor
      servicioDiv.dataset.idServicio = id;

      //Selecciona Servicio

      servicioDiv.onclick = seleccionarServicio;

      //Inyectar precio y nombre
      servicioDiv.appendChild(nombreServicio);
      servicioDiv.appendChild(precioServicio);

      //Inyectar id

      document.querySelector("#servicios").appendChild(servicioDiv);
    });
  } catch (error) {
    console.log(error);
  }
}

function seleccionarServicio(e) {
  let elemento;

  if (e.target.tagName === "P") {
    elemento = e.target.parentElement;
  } else {
    elemento = e.target;
  }

  if (elemento.classList.contains("seleccionado")) {
    elemento.classList.remove("seleccionado");

    console.log(elemento.dataset.idServicio);
    const id = parseInt(elemento.dataset.idServicio);
    eliminarServicio(id);
  } else {
    elemento.classList.add("seleccionado");
    // console.log(elemento.firstElementChild.nextElementSibling.textContent);

    const servicioObj = {
      id: parseInt(elemento.dataset.idServicio),
      nombre: elemento.firstElementChild.textContent,
      precio: elemento.firstElementChild.nextElementSibling.textContent,
    };

    // console.log(servicioObj);
    agregarServicio(servicioObj);
  }

  // console.log(elemento.dataset.idServicio);
}

function eliminarServicio(id) {
  const { servicios } = cita;

  cita.servicios = servicios.filter((servicio) => servicio.id !== id);

  console.log(cita);
}

function agregarServicio(servicioObj) {
  const { servicios } = cita;

  cita.servicios = [...servicios, servicioObj];

  console.log(cita);
}
function paginaAnterior() {
  const botonAnterior = document.querySelector("#paginaAnterior");

  botonAnterior.addEventListener("click", () => {
    pagina--;
    paginador();
  });
}

function paginaSiguiente() {
  const botonSiguiente = document.querySelector("#paginaSiguiente");

  botonSiguiente.addEventListener("click", () => {
    pagina++;
    paginador();
  });
}

function paginador() {
  const botonAnterior = document.querySelector("#paginaAnterior");
  const botonSiguiente = document.querySelector("#paginaSiguiente");

  if (pagina === 1) {
    botonAnterior.classList.add("ocultar");
    botonSiguiente.classList.remove("ocultar");
  } else if (pagina === 3) {
    botonSiguiente.classList.add("ocultar");
    botonAnterior.classList.remove("ocultar");
    mostrarResumen(); //Estamos en la pagina tres carga el resumen de la cita
  } else {
    botonAnterior.classList.remove("ocultar");
    botonSiguiente.classList.remove("ocultar");
  }

  mostrarSeccion(); //Cambia la seccion que se muestra por la de la pagina
}

function mostrarResumen() {
  //Destructiring

  const { nombre, fecha, hora, servicios } = cita;

  //Selecciona el resumen
  const resumenDiv = document.querySelector(".contenido-resumen");

  //Limpiar el HTML previo

  while (resumenDiv.firstChild) {
    resumenDiv.removeChild(resumenDiv.firstChild);
  }

  //Se valida si el obejto esta vacio

  if (Object.values(cita).includes("")) {
    //Se crean los elementos que se insertaran en el HTML
    const noServicio = document.createElement("DIV");
    const textoNoServicio = document.createElement("P");
    const errorImage = document.createElement("I");

    //Se agrgan el texto y las imagenes
    textoNoServicio.textContent =
      "No se ha agregado información de Nombre, Fecha, Hora.";
    errorImage.classList.add("fas", "fa-times");

    // Se agregan 'P' e 'I' al DIV
    noServicio.appendChild(textoNoServicio);
    noServicio.appendChild(errorImage);

    //Se crea variable resumen seleccionando clase resumen del html
    noServicio.classList.add("invalidar-cita");

    //Insertando al HTML con la clase .resumen

    resumenDiv.appendChild(noServicio);
  } else {
    const headingDatos = document.createElement("H3");
    headingDatos.textContent = "Resumen de cita";

    const nombreCita = document.createElement("P");
    nombreCita.innerHTML = `<span>Nombre:</span> ${nombre}`;

    const fechaCita = document.createElement("P");
    fechaCita.innerHTML = `<span>Fecha:</span> ${fecha}`;

    const horaCita = document.createElement("P");
    horaCita.innerHTML = `<span>Hora:</span> ${hora}`;

    const headingServicios = document.createElement("H3");
    headingServicios.textContent = "Resumen de Servicios";

    let cantidad = 0;

    const totalAPagar = document.createElement("P");
    totalAPagar.classList.add("pago");

    // iterar sobre el arreglo de servicios

    const resumenServicios = document.createElement("DIV");
    resumenServicios.classList.add("resumen-servicios");

    servicios.forEach((servicio) => {
      const { nombre, precio } = servicio;

      const divServicios = document.createElement("DIV");
      divServicios.classList.add("contenedor-servicio");

      const nombreServicio = document.createElement("P");
      nombreServicio.textContent = `${nombre}`;

      const precioServicio = document.createElement("P");
      precioServicio.textContent = `${precio}`;
      precioServicio.classList.add("resumen-precio");

      const totalServicio = precio.split("$");

      console.log(totalServicio[1].trim());

      cantidad += parseInt(totalServicio[1].trim());

      divServicios.appendChild(nombreServicio);
      divServicios.appendChild(precioServicio);
      resumenServicios.appendChild(divServicios);
    });
    totalAPagar.innerHTML = `<span>Total a Pagar: </span> $ ${cantidad}`;
    console.log(cantidad);
    resumenDiv.appendChild(headingDatos);
    resumenDiv.appendChild(nombreCita);
    resumenDiv.appendChild(fechaCita);
    resumenDiv.appendChild(horaCita);
    resumenDiv.appendChild(headingServicios);
    resumenDiv.appendChild(resumenServicios);
    resumenDiv.appendChild(totalAPagar);
  }
}

function nombreCita() {
  const nombreInput = document.querySelector("#nombre");

  nombreInput.addEventListener("keypress", (e) => {
    if (e.keyCode == 13) {
      e.preventDefault();
    }
  });

  nombreInput.addEventListener("input", (e) => {
    const nombreTexto = e.target.value.trim();
    //Prevenir qu ela pagina se recargue cuando se da enter

    if (nombreTexto === "" || nombreTexto.length < 3) {
      mostrarAlerta("Nombre no valido", "error", "nombre");
      cita.nombre = nombreTexto;
    } else {
      const alertaInput = document.querySelector("#nombre.alerta-input");
      const alerta = document.querySelector(".alerta");

      if (alertaInput) {
        alertaInput.classList.remove("alerta-input");
      }
      if (alerta) {
        alertaInput.classList.remove("alerta");
      }

      cita.nombre = nombreTexto;
      console.log(cita);
    }
  });
}

function mostrarAlerta(mensaje, tipo, idInput) {
  //Si hay una alerta previa entonces no agregar otra

  const alertaPrevia = document.querySelector(".alerta");
  const alertaInput = document.querySelector(`#${idInput}`);
  if (alertaPrevia) {
    return;
  }
  const alerta = document.createElement("DIV");
  alerta.textContent = mensaje;
  alerta.classList.add("alerta");

  if (tipo === "error") {
    alerta.classList.add("error");
  }

  alertaInput.classList.add("alerta-input");

  //Insertar en el HTML

  const formulario = document.querySelector(".formulario");
  formulario.appendChild(alerta);

  //Eliminar la alerta despues de 3s

  setTimeout(() => {
    alerta.remove();
  }, 4000);
}

function fechaCita() {
  const fechaInput = document.querySelector("#fecha");
  fechaInput.addEventListener("input", (e) => {
    //Objeto de fecha
    const dia = new Date(e.target.value).getDay();

    // configurar fecha
    // const opciones = {
    //   year: "numeric",
    //   month: "long",
    //   weekday: "long",

    // };

    // console.log(dia.toLocaleDateString("es-Es", opciones));

    //Validar dia de la semana

    if ([5, 6].includes(dia)) {
      e.preventDefault();
      fechaInput.value = "";
      mostrarAlerta("Día no valido", "error");
    } else {
      cita.fecha = fechaInput.value;
    }
  });
}

function desabilitaFechaAnterior() {
  const inputFecha = document.querySelector("#fecha");

  const fechaAhora = new Date();
  const year = fechaAhora.getFullYear();
  const mes =
    fechaAhora.getDate() === 31
      ? fechaAhora.getMonth() + 2
      : fechaAhora.getMonth() + 1;

  const dia = fechaAhora.getDate() === 31 ? 1 : fechaAhora.getDate() + 1;

  //Formato deseado : AAAA-MM-DD

  const fechaDesabilitar = `${year}-${mes < 10 ? `0${mes}` : mes}-${
    dia < 10 ? `0${dia}` : dia
  }`;

  // const fechaDesabilitar = `${year}-${mes < 10 ? `0${mes}` : mes}-${
  //   dia < 10 ? `0${dia}` : dia
  // }`;

  inputFecha.min = fechaDesabilitar; // desabilitar fechas anteriores
}

function horaCita() {
  const inputHora = document.querySelector("#hora");

  inputHora.addEventListener("input", (e) => {
    const horaCita = e.target.value;
    const hora = horaCita.split(":");

    if (hora[0] < 10 || hora[0] >= 20) {
      mostrarAlerta(
        "Hora no valida, por favor elija una hora entre las 10:00 a.m y las 7:59 p.m",
        "error",
        "hora"
      );
      cita.hora = "";
      console.log("Hora 0");
      return;
    }
    //Valida si la alerta esta activa y la desactiva
    const alertaInput = document.querySelector("#hora.alerta-input");
    const alerta = document.querySelector(".alerta");

    if (alertaInput) {
      alertaInput.classList.remove("alerta-input");

      if (alerta) {
        alerta.remove();
      }
    }
    cita.hora = horaCita;
  });
}
